.footer__container{
    width: 100%;
    background-color:#FFF5F6;

}
.footer__header{
font-family: 'Titan One';
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 1.16;
text-transform: uppercase;

color: #FFA5BA;

padding-left: 22px;
padding-top: 40px;
width: 232px;

}
.footer__text{
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    
    color: #D41443;
    
    padding-left: 20px;
    margin-top: 16px;
    width: 200px;



    
}
.footer__link{
    text-decoration: none;
    margin-right: 28px;

}

.footer__list{
    display: flex;
    justify-content: flex-end;
    margin-bottom: 40px;
    margin-top: 40px;
  
}

.footer__item{
    list-style: none;
}
.footer__nav-text{
font-family: 'DM Sans';
font-style: normal;
font-weight: 700;
font-size: 12px;
line-height: 186%;
text-align: right;

padding-right: 20px;

color: #000000;

}
.footer__nav{
font-family: 'DM Sans';
font-style: normal;
font-weight: 700;
font-size: 12px;
line-height: 186%;

text-align: right;
color: #D41443;



}

.footer-rights{
  
font-family: 'DM Sans';
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 152%;

text-align: center;

color: #907E82;
border-top: 1px solid #E1E1E1;
padding-top: 16px;
margin-top: 40px;
margin-bottom: 3px;

}

.footer__rights-text{
    display: block;
}